tés compétentes en matière d'immigration et d'asile dans les États membres aux fins de la vérification du respect des conditions d'entrée et de séjour réguliers sur le territoire des États membres, aux fins de l'identification des personnes qui ne remplissent pas ou plus ces conditions, aux fins de l'examen d'une demande d'asile et de la détermination de l'autorité responsable de cet examen. Dans certaines conditions, ces données seront aussi accessibles aux autorités désignées des États membres et à Europol aux fins de la prévention et de la détection des infractions terroristes et des autres infractions pénales graves, ainsi qu'aux fins des enquêtes en la matière. Les autorités compétentes pour le traitement des données sont le Ministère des relations extérieurs au Cameroun (VGH8+G34, Yaoundé, Cameroun) et le Ministère des Affaires Etrangères (VGH8+G34, Yaoundé, Cameroun). Je suis informé(e) de mon droit d'obtenir auprès de IVISAS-AFFAIRES la notification des données me concernant qui sont enregistrées dans les services ainsi que de l'État membre qui les a transmises, et de demander que les données me concernant soient rectifiées si elles sont erronées . À ma demande expresse, l'autorité qui a examiné ma demande m'informera de la manière dont je peux exercer mon droit de vérifier les données à caractère personnel me concernant et de les faire rectifier ou supprimer, y compris des voies de recours prévues à cet égard par la législation nationale de l'État concerné. L'autorité de contrôle nationale du Cameroun pourra être saisie des demandes concernant la protection des données Je déclare qu'à ma connaissance, toutes les indications que j'ai fournies sont correctes et complètes. Je suis informé(e) que toute fausse déclaration entraînera le rejet de mademande ou l'annulation du visa s'il a déjà été délivré, et peut entraîner des poursuites pénales à mon égard en application du droit de l'État membre qui traite la demande. Je m'engage à quitter le territoire du Cameroun avant l'expiration du visa, si celui-ci m'est délivré. J'ai été informé(e) que la possession d'un visa n'est que l'une des conditions de l'entrée sur le territoire Camerounais. Le simple fait qu'un visa m'ait été accordé n'implique pas que j'aurai droit attachés aux citoyens du territoire, je dois prendre des informations auprès des services compétentes pour connaitre les droits qui me sont accordés. ")]),_c('div',{staticClass:"cell1"},[_vm._v("Lieu et date :"),_c('br'),_vm._v(" Lieu : "),_c('span',{staticStyle:{"font-weight":"700"}},[_vm._v(_vm._s(this.homeAddress)+" ")]),_c('br'),_vm._v(" Date : "),_c('span',{staticStyle:{"font-weight":"700"}},[_vm._v(_vm._s(this.sendedDate)+" ")])]),_c('div',{staticClass:"cell1"},[_vm._v("Signature (pour les mineurs, signature de l’autorité parentale/du tuteur légal) : ")])])])])]),_c('div',{staticClass:"d-flex flex-column flex-lg-row h-lg-full bg-surface-secondary"},[_c('nav',{staticClass:"navbar show navbar-vertical h-lg-screen navbar-expand-lg px-0 py-3 navbar-light bg-white border-bottom border-bottom-lg-0 border-end-lg",attrs:{"id":"navbarVertical"}},[_c('div',{staticClass:"container-fluid"},[_vm._m(0),_c('router-link',{attrs:{"to":"/AdminDashboard"}},[_c('a',{staticClass:"navbar-logo py-lg-2 mb-lg-5 px-lg-12 me-0",attrs:{"href":"#"}},[_c('img',{attrs:{"src":require("@/assets/logo-jpg.png"),"alt":"..."}})])]),_c('div',{staticClass:"collapse navbar-collapse",attrs:{"id":"sidebarCollapse"}},[_c('ul',{staticClass:"navbar-nav"},[_c('router-link',{attrs:{"to":"/AdminDashboard"}},[_c('li',{staticClass:"nav-item"},[_c('a',{staticClass:"nav-link active"},[_c('i',{staticClass:"bi bi-house"}),_vm._v(" Tableau de bord ")])])]),_c('router-link',{attrs:{"to":"/AdminAllDevis"}},[_c('li',[_c('a',{staticClass:"nav-link"},[_c('i',{staticClass:"bi bi-pencil-square"}),_vm._v(" Demandes des devis ")])])]),_c('router-link',{attrs:{"to":"/AdminAllVisas"}},[_c('li',{staticClass:"nav-item"},[_c('a',{staticClass:"nav-link"},[_c('i',{staticClass:"